//h1 {
#flicker-github {
	font-family: 'CoreCircus', sans-serif;
	font-size: 42px; //14vw;
	text-align: center;
	line-height: 1;
	margin: 0;
	transform: translate(-50%, -50%);
	position: absolute;
	color: $h1;
	letter-spacing: -.5rem;
    //max-width: 600px;
    left: 36%;
    top: 37%;
	
	&:before {
		content: attr(data-heading);
		position: absolute;
		overflow: hidden;
		color: $lime;
		width: 100%;
		z-index: 5;
		text-shadow: none;
		left: 0%; //22%;
		text-align: left;
		animation: flicker 5s linear infinite;
	}
}

#flicker-linkedin {
	font-family: 'CoreCircus', sans-serif;
	font-size: 42px; //14vw;
	text-align: center;
	line-height: 1;
	margin: 0;
	//top: 50%;
	//left: 50%;
	transform: translate(-50%, -50%);
	position: absolute;
	color: $h1;
	letter-spacing: -.5rem;
    //max-width: 600px;
    left: 72%;
    top: 37%;
	
	&:before {
		content: attr(data-heading);
		position: absolute;
		overflow: hidden;
		color: $yellow;
		width: 100%;
		z-index: 5;
		text-shadow: none;
		left: 12%;
		text-align: left;
		animation: flicker 5s linear infinite;
	}
}

#flicker-insta {
	font-family: 'CoreCircus', sans-serif;
	font-size: 27px; //14vw;
	text-align: center;
	line-height: 1;
	margin: 0;
	//top: 50%;
	//left: 50%;
	transform: translate(-50%, -50%);
	position: absolute;
	color: $h1;
	letter-spacing: -.5rem;
    //max-width: 600px;
    left: 86%;
    top: 41%;
	
	&:before {
		content: attr(data-heading);
		position: absolute;
		overflow: hidden;
		color: $redorange;
		width: 100%;
		z-index: 5;
		text-shadow: none;
		left: 0%;
		text-align: left;
		animation: flicker 5s linear infinite;
	}
}


@keyframes flicker {
	0%, 19.999%, 22%, 62.999%, 64%, 64.999%, 70%, 100% {
		opacity: .99;
		text-shadow: -1px -1px 0 $outline, 1px -1px 0 $outline, -1px 1px 0 $outline, 1px 1px 0 $outline, 0 -2px 8px, 0 0 2px, 0 0 5px #ff7e00, 0 0 15px #ff4444, 0 0 2px #ff7e00, 0 2px 3px #000;
	}
	20%, 21.999%, 63%, 63.999%, 65%, 69.999% {
		opacity: 0.4;
		text-shadow: none;
	}
}

